import React, { useState } from "react";  
import "./styles/Hero.css";
import RadioBtn from "./RadioBtn";
import SliderArea from "./Slider";
import BtnStrenght from "./BtnStrenght";
import BtnMain from "./BtnMain";
import PasswordBox from "./PasswordBox";
import Footer from './Footer'

export default function Hero(){

    const [brojac1 , setBrojac1]=useState(false);
    const [brojac2 , setBrojac2]=useState(false);
    const [brojac3 , setBrojac3]=useState(false);
    const [brojac4 , setBrojac4]=useState(false);
    const [length , setLenght]=useState(10);
    const [password , setPassword]=useState('');

    return (
        <div>
        <div className="container">
            <h1 className="h1-main">
                Password Generator
            </h1>

            <div className="Pass-area">
                <PasswordBox
                    password={password} />
            </div>

            <div className="Check-area">
                <SliderArea
                    setLenght={setLenght} />
                <RadioBtn
                    setBrojac1={setBrojac1}
                    setBrojac2={setBrojac2}
                    setBrojac3={setBrojac3}
                    setBrojac4={setBrojac4} />
                <BtnStrenght
                    brojac1={brojac1}
                    brojac2={brojac2}
                    brojac3={brojac3}
                    brojac4={brojac4} />


                <BtnMain
                    length={length}
                    brojac1={brojac1}
                    brojac2={brojac2}
                    brojac3={brojac3}
                    brojac4={brojac4}
                    setPassword={setPassword}
                    password={password} />




            </div>

        </div>
        
        <Footer />
        </div>
    )

};