import React from "react";  
import FormGroup from '@mui/material/FormGroup';
import './styles/RadioBtn.css'
import ChecBtn from "./CheckBox";



export default function RadioBtn({setBrojac1,setBrojac2,setBrojac3,setBrojac4}){


  


    return(
        <div className="RadioBtn">

            <div>
            <FormGroup>
           <ChecBtn
            labeltext={"Include Uppercase Letters"}
            setBrojac={setBrojac1}
           />
           <ChecBtn
            labeltext={"Include Lowercase Letters"}
            setBrojac={setBrojac2}
           />
           <ChecBtn
            labeltext={"Include Numbers"}
            setBrojac={setBrojac3}
           />
           <ChecBtn
            labeltext={"Include Symbols"}
            setBrojac={setBrojac4}
           />
           </FormGroup>
            </div>

        </div>
    )

};