import React from "react";
import './styles/BtnStrenght.css'

export default function BtnStrenght({brojac1,brojac2,brojac3,brojac4}){

    let br=0;
    let StrenghtText ="";

  if(brojac1){
    br++;
  }if(brojac2){
    br++
  }if(brojac3){
    br++
  }if(brojac4){
    br++
  };

  if(br===1){
    StrenghtText="TOO WEAK"
  } if(br===2){
    StrenghtText="WEAK"
  }if(br===3){
    StrenghtText="MEDIUM"
  }if(br===4){
    StrenghtText="STRONG"
  }if(br===0) {
    StrenghtText=""
  }
    
    return(

        <div className="Strenght-Box">
            <h1 className="h1-main">STRENGTH</h1>
            <div className="Wrapper">
            <h2 className="h2-main-text">{StrenghtText}</h2>
            <div className="Wrapper-line">
                <div className={(br===1)?"lineRed":(br===2)?"lineOrange":(br===3)?"lineYellow":(br===4)?"lineGreen":"line"}></div>
                <div className={(br===2)?"lineOrange":(br===3)?"lineYellow":(br===4)?"lineGreen":"line"}></div>
                <div className={(br===3)?"lineYellow":(br===4)?"lineGreen":"line"}></div>
                <div className={(br===4)?"lineGreen":"line"}></div>
            </div>
            </div>
        </div>
    )

};