import React, { useState } from "react";
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import './styles/RadioBtn.css'


export default function ChecBtn({labeltext,setBrojac}){

    const [checked , setChecked] = useState(false);


        return (

          <div className="checkboxWrapper">

            <FormControlLabel control={<Checkbox checked={checked} onChange={e=>{
                            console.log("target checked1 - ", e.target.checked);
                            setChecked(e.target.checked)
                            setBrojac(e.target.checked)
                          }} sx={{
                  color: '#E6E5EA',
                  '&.Mui-checked': {
                    color: '#A4FFAF',
                  },
                }} />} />

            <div>
                  <h2 className="h2-check">{labeltext}</h2>
            </div>


          </div>
           
        )

}